.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.btn-primary{
    margin: 5px 0 25px;
}

.main-section{
    margin: 0 auto;
    margin-top: 25%;
    padding: 0;
}
.modal-content{
    background-color: #3b4652;
    opacity: .85;
    padding: 0 20px;
    box-shadow: 0px 0px 3px #848484;
    -moz-box-shadow: 0px 0px 3px #848484;
    -webkit-box-shadow: 0px 0px 3px #848484;
    margin-top: 50px;
    
}


.user-img{
    margin-top: -50px;
    margin-bottom:5px;
}

.gap-2{
    margin-top: 10px;
    margin-bottom: 10px;
}

.user-img img{
    width: 100px;
    height: 100px;
    box-shadow: 0px 0px 3px #848484;
    -moz-box-shadow: 0px 0px 3px #848484;
    -webkit-box-shadow: 0px 0px 3px #848484;
    border-radius: 50%;
}

.form-group input{
    height: 42px;
    font-size: 18px;
    border: 0;
    border-radius: 5px;
}

.forgot{
    padding: 5px 0;
}

.forgot a{
    color: #fff;
    text-decoration: none;
}

.forgot a:hover{
    color: black;
}